let io = new IntersectionObserver( function(entries) {
        entries.forEach(function(item){
            console.log(1)
            if(item.intersectionRatio>0){
                item.target.style.cssText="visibility: visible;animation-duration: 2s;"
             }
            //  io.unobserve(item.target);
        })
        
    })
    setTimeout(() => {
        let wow = document.getElementsByClassName('wow');
        let wowArr = Array.from(wow)
        wowArr.forEach(item => {
          io.observe(item)
        })
    }, 100);
